import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";


import UserProfile from "./component/profile";
import UserContent from "./component/userContent";
import MyJobs from "./component/myJobs";


class UserLander extends Component {

	constructor(){
		super();
		this.handleLogout = this.handleLogout.bind(this);
	}
	
	componentDidMount() {
        if(localStorage.getItem('user_type') === "seeker"){
            return null 
        } else{
            this.props.history.push('/')
        }
    }
	
handleLogout(){
	localStorage.removeItem('job_email')
	localStorage.removeItem('user_type')
	this.props.history.push('/')
	window.location.reload()
}


  render() {
    return (<>
    <div className="m-4"></div>
    <section className="py-5 my-5">
		<div className="container-fluid">
			<div className="bg-white shadow rounded-lg d-block d-sm-flex">
				<div id="sidebar">
					<div className="profile-tab-nav border-right ">
						<div className="p-4">
							<div className="img-circle text-center mb-3">
								<img src="img/user2.jpg" alt="usermomo" className="shadow"/>
							</div>
							<h4 className="text-center">Kiran Acharya</h4>
						</div>
						<div className="nav flex-column nav-pills mb-4" id="v-pills-tab" role="tablist" aria-orientation="vertical">
							<a className="nav-link " id="account-tab" data-toggle="pill" href="/users/dashboard" role="tab" aria-controls="account" aria-selected="true">
								<i className="fa fa-home text-center mr-1"></i> 
								Home
							</a>
							<a className="nav-link" id="password-tab" data-toggle="pill" href="/users/dashboard/profile" role="tab" aria-controls="password" aria-selected="false">
								<i className="fa fa-key text-center mr-1"></i> 
								Profile
							</a>
							<a className="nav-link" id="security-tab" data-toggle="pill" href="/users/dashboard/myjobs" role="tab" aria-controls="security" aria-selected="false">
								<i className="fa fa-user text-center mr-1"></i> 
								My Jobs
							</a>
							<a className="nav-link" id="security-tab" data-toggle="pill" onClick={this.handleLogout} href="/" role="tab" aria-controls="security" aria-selected="false">
								<i className="fa fa-sign-out text-center mr-1"></i> 
								Logout
							</a>
						</div>
					</div>
				</div>
				<div className="tab-content p-4 p-md-5" id="v-pills-tabContent">
				<Switch>
					<Route exact path="/users/dashboard" component={UserContent}/>
					<Route exact path="/users/dashboard/profile" component={UserProfile}/>
					<Route exact path="/users/dashboard/myjobs" component={MyJobs}/>
				</Switch>
				</div>
			</div>
		</div>
	</section>
    </>);
  }
}

export default UserLander;
