import React, { Component } from "react";
import {
  FacebookLoginButton,
  InstagramLoginButton,
  GoogleLoginButton
} from "react-social-login-buttons";


class Login extends Component {
  constructor() {
    super();

    this.state = {
      email: "",
      password: "",
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    let target = event.target;
    let value = target.type === "checkbox" ? target.checked : target.value;
    let name = target.name;

    this.setState({
      [name]: value
    });
  }

  componentDidMount(){
  }

  handleSubmit(event) {
    event.preventDefault();
    console.log(this.state);
    if (this.state.email==="prsnthmailbox@gmail.com" && this.state.password==="prs"){
      localStorage.setItem('job_email',this.state.email)
      localStorage.setItem('user_type','seeker')
      this.props.history.push('/')
      window.location.reload()
    }
    else if (this.state.email==="bungalowarch3d@gmail.com" && this.state.password==="prs"){
        localStorage.setItem('employ_email',this.state.email)
        localStorage.setItem('user_type','employer')
        this.props.history.push('/employers/dashboard')
        window.location.reload() }  
    else{
      console.log("failed")
    }
  }

  render() {
    return (<>
      <div className="App d-flex">
        <div className="appAside" >
        </div>
          <div className="appForm">
              <div label="Sign-In">
                <div className="formCenter">
                  <form className="formFields" onSubmit={this.handleSubmit}>
                    <div className="formField">
                      <label className="formFieldLabel" htmlFor="email">
                        E-Mail Address
                      </label>
                      <input
                        type="email"
                        id="email"
                        className="formFieldInput"
                        placeholder="Enter your email"
                        name="email" required='True'
                        value={this.state.email}
                        onChange={this.handleChange}
                      />
                    </div>

                    <div className="formField">
                      <label className="formFieldLabel" htmlFor="password">
                        Password
                      </label>
                      <input
                        type="password"
                        id="password"
                        className="formFieldInput"
                        placeholder="Enter your password"
                        name="password" required='True'
                        value={this.state.password}
                        onChange={this.handleChange}
                      />
                    </div>

                    <div className="formField">
                      <button className="formFieldButton" type="submit">Sign In</button>{" "}
                    </div>

                    
                    <div className="socialMediaButtons">
                    <div className="googleButton m-2">
                        <GoogleLoginButton onClick={() => alert("Hello")} />
                      </div>
                      <div className="facebookButton m-2">
                        <FacebookLoginButton onClick={() => alert("Hello")} />
                      </div>

                      <div className="instagramButton m-2">
                        <InstagramLoginButton onClick={() => alert("Hello")} />
                      </div>
                    </div>
                  </form>
                </div>
              </div>
      </div>
    </div>
    </>);
  }
}

export default Login;
